@media screen and (max-width:1110px)  {
    .header{
        position: sticky;
        top: 0;
        left: 0;
        height: 4rem;
        display: flex;
        width: 100vw;
        justify-content: center;
        align-items: center;
        background-color: rgb(19, 19, 19);
    }
    
    input{
        height: 2.4rem;
        border-radius: 5px;
        outline: none;
        border: 2px solid grey;
        background-color: rgb(71, 71, 71);
        font-size: 1.3rem;
        color: rgb(255, 255, 255);
    }
}
